                            <stop offset='90%' stop-color='#f00'></stop> 
                            <stop offset='100%' stop-color='#f00'></stop>
                        </radialGradient> 
                    <defs>
                    <rect width="300" height="300" fill="url(#rg)"></rect>
                </svg>
                */
                // 特殊字符转换的规则如下
                // '&' -> '%26'
                // '|' -> '%7C'
                // '[' -> '%5B'
                // ']' -> '%5D'
                // '^' -> '%5E'
                // '`' -> '%60'
                // ';' -> '%3B'
                // '?' -> '%3F'
                // ':' -> '%3A'
                // '@' -> '%40'
                // '=' -> '%3D'
                let xlinkHref = "data:image/svg+xml;utf8,%3Csvg version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='300' height='300'%3E%3Cdefs%3E%3CradialGradient id='rg' r='.9'%3E";
                for (var i = 0; i < 11; i++) {
                    xlinkHref += `%3Cstop 
                                offset='${(i - 2) * 10 + displacement}%25' 
                                stop%2Dcolor='%23${i % 2 == 0 ? "3f0" : "000"}'%3E%3C/stop%3E`;
                }
                xlinkHref +=
                    "%3C/radialGradient%3E%3C/defs%3E%3Crect width='300' height='300' fill='url(%23rg)'%3E%3C/rect%3E%3C/svg%3E";
                return xlinkHref;
            }
            function AnimateOffset() {
                let xlinkHref = generateGradient();
                feImage.setAttributeNS(xlink, "href", xlinkHref);
                if (displacement <= 20) {
                    displacement += speed;
                } else {
                    displacement = 0;
                }
                window.requestAnimationFrame(AnimateOffset);
            }
            AnimateOffset();
            window.requestAnimationFrame(AnimateOffset);
        }
    },
