<template>
    <v-container fluid fill-height style="padding:0;" id="wave" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Explain">解释</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <p class=".body-1">1. <code>save()</code> 保存cavas状态</p>
                                    <p class=".body-1">2. <code>restore()</code> 弹出栈中保存的canvas状态</p>
                                    <p class=".body-1">3. <code>requestAnimationFrame()</code> 执行动画，一般1s执行60次</p>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
                <v-layout row wrap>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Result1">效果</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <canvas id="canvas" width="800px" height="500px"></canvas>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Code1">代码</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                    <v-tab key="html" ripple>HTML</v-tab>
                                    <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                    <v-tab-item key="html">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">
                                                    {{html}}
                                                </pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                    <v-tab-item key="js">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">{{js}}</pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                </v-tabs>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-9-30",
            title: "利用贝塞尔曲线绘制流动的波浪",
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
            ],
            html: `
            <canvas 
                id="canvas" 
                width="800px" 
                height="500px"
            >
            </canvas>
            `,
            js: `
            const canvas = document.getElementById('canvas');
            const ctx = canvas.getContext('2d');
            ctx.strokeStyle = 'darkseagreen';
            ctx.fillStyle = 'green';
            let step = 0;
            let tmp = 0.5;
            
            window.requestAnimationFrame(draw);
            function draw() {
                ctx.save();
                ctx.clearRect(0, 0, canvas.width, canvas.height);
                ctx.beginPath();
                if(step >= 40) {
                    tmp = -0.5;
                } else if (step <= -40) {
                    tmp = 0.5;
                }
                step += tmp;
                ctx.moveTo(10, 100);
                ctx.bezierCurveTo(30, 80 + step, 70, 150 - step, 100, 100);
                ctx.bezierCurveTo(110, 80 + step, 150, 150 - step, 190, 100);
                ctx.bezierCurveTo(190, 80 + step, 230, 150 - step, 270, 100);
                ctx.lineTo(270, 200);
                ctx.lineTo(10, 200);
                ctx.lineTo(10, 100);
                ctx.fill();
                ctx.restore();
                window.requestAnimationFrame(draw);
            }
            `
        };
    },
    mounted() {
        const canvas = document.getElementById('canvas');
        const ctx = canvas.getContext('2d');
        ctx.strokeStyle = 'darkseagreen';
        ctx.fillStyle = 'green';
        let step = 0;
        let tmp = 0.5;
        
        window.requestAnimationFrame(draw);
        function draw() {
            ctx.save();
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            ctx.beginPath();
            if(step >= 40) {
                tmp = -0.5;
            } else if (step <= -40) {
                tmp = 0.5;
            }
            step += tmp;
            ctx.moveTo(10, 100);
            ctx.bezierCurveTo(30, 80 + step, 70, 150 - step, 100, 100);
            ctx.bezierCurveTo(110, 80 + step, 150, 150 - step, 190, 100);
            ctx.bezierCurveTo(190, 80 + step, 230, 150 - step, 270, 100);
            ctx.lineTo(270, 200);
            ctx.lineTo(10, 200);
            ctx.lineTo(10, 100);
            ctx.fill();
            ctx.restore();
            window.requestAnimationFrame(draw);
        }
    }
};
</script>

